import React, {memo} from 'react';
import {goBack, styled, useNavigationParams} from '@true/core';
import {Comment, DynamicHeader, FullScreenWrapper, UIButton} from '@true/ui';
import {View} from 'react-native';
import {Icons} from '@true/asset';

interface Props {
  hid?: string;
  token?: string;
  appName?: string;
  type?: 'comment' | 'post';
}

export const CommentScreen = memo(() => {
  const {hid, token, type, appName} = useNavigationParams<Props>();
  return (
    <FullScreenWrapper>
      <Comment.Provider hid={hid} token={token} appName={appName} type={type}>
        <DynamicHeader
          title={'Comment'}
          leftComponent={<View />}
          rightComponent={<SCloseButton onPress={goBack} />}
        />
        <Comment.List appName={appName} />
        <Comment.Input />
        <Comment.BottomSheet />
      </Comment.Provider>
    </FullScreenWrapper>
  );
});

const SCloseButton = styled(UIButton).attrs(p => ({
  source: Icons.Outlined.X_MARK,
  imageSize: 24,
  imageStyle: {
    tintColor: p.theme.neutral1,
  },
}))`
  padding-right: 16px;
`;
